





























































import { Component, Vue, Prop, Emit } from "vue-property-decorator";
@Component({
  name: "Optimization",
  components: {},
})
export default class Optimization extends Vue {
  private Opt: any = {
    starttime: "", // 
    Duration: "", // 
    disasterType: "", // 
    disasterCoordinate: "", // 
    SourceCoordinate: "", // 
    SourceLevel: "", // 
    fractureDirection: "", //
    landslideLevel: "", // 
    precipitation: "", // 
    buildingCollapse: "", // 
    fire: "",
  };

  @Prop() private defaultData?: any;

  private mounted() {
    if (this.defaultData) {
      this.Opt = this.defaultData;
    }
  }

  // 
  private empty() {
    (this.$refs["form"] as any).resetFields();
  }

  // 
  private submit() {
    let data: any = {
      data: {},
      index: -1,
    };
    this.setIndex(data);
  }
  // 
  private goback() {
    let data: any = {
      data: {
        Optimization: this.Opt,
      },
      index: 3,
    };
    this.setIndex(data);
  }

  @Emit("setPanelView")
  private setIndex(data: any) {
    return data;
  }
}
