
















import { Component, Vue, Prop, Emit } from "vue-property-decorator";
@Component({
  name: "extract",
  components: {},
})
export default class extract extends Vue {
  private textarea: any =
    "20206259113.6445622222.4092707651";
  @Prop() private defaultData?: any;

  private mounted() {
    if (this.defaultData) {
      this.textarea = this.defaultData;
    }
  }
  // 
  private VoiceInput() {
    console.log("");
  }
  // 
  private empty() {
    this.textarea = "";
  }
  // 
  private submit() {
    if (!this.textarea) {
      this.$message.warning("");
      return;
    }
    let data: any = {
      data: {
        textarea: this.textarea,
      },
      index: 2,
    };
    this.setIndex(data);
    let datas: any = [];
    datas.push({
      longitude: 113.97293464,
      latitude:22.5880109,
      id: "center",
      tag: "null",
      symbol: "",
      type: "",
    });
    this.$Bus.$emit("focus", datas, 15);
    this.$Bus.$emit("addArea", "Before");
  }

  @Emit("setPanelView")
  private setIndex(data: any) {
    return data;
  }
}
